@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');
.caseBox {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    background: #ffa0a083;
    padding: 40px 15px !important;
    color: #ff2626;
    justify-content: center;
    align-items: center;
    width: 25vw;
    max-width: 220px;
    min-width: 150px;
    margin: 15px 10px;
}
/* .caseBox * {
    background: rgba(255, 160, 160, 0.514);
} */
.case-title {
    font-size: 1rem;
    margin-bottom: 0.8rem;
    font-weight: 600;
}
.case-count {
    font-size: 1.7rem;
    margin: 5px;
    font-weight: 600;
    font-family: 'Roboto Mono', monospace;
    /* animation: typing 1.5s steps(40,end) , blink-caret .75s step-end; */
}
@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
    100% { display: none; }
}
.case-today {
    font-size: 0.8rem;
    margin-top: 8px;
    font-weight: 600;
    font-family: 'Roboto Mono', monospace;
}

@media screen and (max-width: 500px) {
    .caseBox {
        width: 150vw;
        max-width: 200px;
      }
    .case-title { font-size: 1rem;}
    .case-count { font-size: 1.6em;}
    .case-today { font-size: 0.7rem;}
}