@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Recursive&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  /* background-color: #EEEBDD; */
  /* color: rgb(10, 13, 56); */
  margin: 0; padding: 0;
  font-family: 'Recursive', sans-serif;
}
.header {
  font-weight: 400;
  font-size: 1.9rem;
  margin: 25px auto 35px;
  text-align: center;
  letter-spacing: 2px;
  text-shadow: 0 0 2px rgb(142, 172, 255);
}
/* Search Line */

.country-selection {
  padding: 10px 17%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 70vw;
}
.country-title {
  color:rgba(10, 13, 56, 0.9);
  font-size: 1.1rem;
}
.country-newline {
  display: none;
}
.country-dropdown {
  flex-grow: 1;
  display: flex;
}
.country-search {
  background: rgba(110, 110, 250, 0.178);
  margin: auto 15px;
  border: none;
  border-radius: 4px;
  padding: 18px 20px 18px 16px;
  font-size: 1rem;
  flex: 1 1;
}
.country-search:after {
  padding: 18px;
  content: '>>';
}
.country-search:focus, .country-search:hover {
  outline: none;
  box-shadow: 0 0 8px 5px rgba(147, 147, 218, 0.144);
  transition-duration: 0.3s;
}

.country-search::-webkit-scrollbar {
  width: 7px;
}
.country-search::-webkit-scrollbar-thumb {
  background: rgba(10, 13, 56, 0.5);
  border-radius: 3px;
}
.country-search::-webkit-scrollbar-thumb:hover {
  background: rgba(10, 13, 56, 0.75);
}

.case-boxes {
  padding: 2% 7%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.last-updated {
  display: flex;align-items: center;justify-content:center;font-size: 0.88rem;color: rgb(29, 30, 31);
}

@media screen and (max-width: 670px) {
  .country-selection {
    padding: 10px 8%;
  }
  .country-title {
    flex-basis: 100%;
    font-size: 0.9rem;
    margin-bottom: 8px;
  }
  .country-search {
    margin-left: 0;
  }
}

@media screen and (max-width: 500px) {
  .case-boxes {
    padding: 2% 3%;
  }
  .country-search {
    width: 100%;
  }
  .country-selection {
    padding: 2% 5%;
  }
}
.caseBox {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    background: #ffa0a083;
    padding: 40px 15px !important;
    color: #ff2626;
    justify-content: center;
    align-items: center;
    width: 25vw;
    max-width: 220px;
    min-width: 150px;
    margin: 15px 10px;
}
/* .caseBox * {
    background: rgba(255, 160, 160, 0.514);
} */
.case-title {
    font-size: 1rem;
    margin-bottom: 0.8rem;
    font-weight: 600;
}
.case-count {
    font-size: 1.7rem;
    margin: 5px;
    font-weight: 600;
    font-family: 'Roboto Mono', monospace;
    /* animation: typing 1.5s steps(40,end) , blink-caret .75s step-end; */
}
@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
}
@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}
@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
    100% { display: none; }
}
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
    100% { display: none; }
}
.case-today {
    font-size: 0.8rem;
    margin-top: 8px;
    font-weight: 600;
    font-family: 'Roboto Mono', monospace;
}

@media screen and (max-width: 500px) {
    .caseBox {
        width: 150vw;
        max-width: 200px;
      }
    .case-title { font-size: 1rem;}
    .case-count { font-size: 1.6em;}
    .case-today { font-size: 0.7rem;}
}
